<template>
  <div class="chat-widget" :class="{ 'widget-open': isOpen }">
    <!-- Botón de chat flotante -->
    <button 
      class="chat-button"
      @click="toggleChat"
      :aria-label="isOpen ? 'Cerrar chat' : 'Abrir chat'"
    >
      <span v-if="!isOpen" class="icon-message">💬</span>
      <span v-else class="icon-close">✕</span>
    </button>

    <!-- Panel de chat -->
    <div class="chat-panel" v-if="isOpen">
      <div class="chat-header">
        <div class="branding">
          <img v-if="logoUrl" :src="logoUrl" alt="Logo" class="logo" width="24" height="24" />
          <h3>{{ widgetTitle }}</h3>
        </div>
        <button class="close-button" @click="toggleChat" aria-label="Cerrar chat">
          <span class="icon-close">✕</span>
        </button>
      </div>

      <div v-if="currentStep === 'welcome'" class="welcome-screen">
        <p>{{ welcomeMessage }}</p>
        <button class="primary-button" @click="currentStep = 'form'">
          Iniciar conversación
        </button>
      </div>

      <div v-else-if="currentStep === 'form'" class="contact-form">
        <form @submit.prevent="submitForm">
          <div class="form-group">
            <label for="name">Nombre</label>
            <input 
              type="text" 
              id="name" 
              v-model="form.name" 
              required
              :class="{'input-error': formErrors.name}"
            >
            <small v-if="formErrors.name" class="error-text">{{ formErrors.name }}</small>
          </div>
          
          <div class="form-group">
            <label for="email">Correo electrónico</label>
            <input 
              type="email" 
              id="email" 
              v-model="form.email" 
              required
              :class="{'input-error': formErrors.email}"
            >
            <small v-if="formErrors.email" class="error-text">{{ formErrors.email }}</small>
          </div>
          
          <div class="form-group">
            <label for="issue">¿En qué podemos ayudarte?</label>
            <textarea 
              id="issue" 
              v-model="form.issue" 
              rows="3" 
              required
              :class="{'input-error': formErrors.issue}"
            ></textarea>
            <small v-if="formErrors.issue" class="error-text">{{ formErrors.issue }}</small>
          </div>
          
          <div class="form-actions">
            <button 
              type="submit" 
              class="primary-button" 
              :disabled="isSubmitting"
            >
              {{ isSubmitting ? 'Enviando...' : 'Enviar' }}
            </button>
          </div>
        </form>
      </div>

      <div v-else-if="currentStep === 'chat'" class="chat-conversation">
        <div class="messages" ref="messagesContainer">
          <div 
            v-for="(message, index) in messages" 
            :key="index" 
            :class="['message', message.isUser ? 'user-message' : 'agent-message']"
          >
            <div class="message-content">{{ message.text }}</div>
            <div class="message-time">{{ formatTime(message.timestamp) }}</div>
          </div>
        </div>
        
        <div class="message-input">
          <form @submit.prevent="sendMessage">
            <input 
              type="text" 
              v-model="newMessage" 
              placeholder="Escribe un mensaje..."
              :disabled="isSubmitting"
            >
            <button 
              type="submit" 
              class="send-button" 
              :disabled="!newMessage.trim() || isSubmitting"
            >
              <span class="icon-send">➤</span>
            </button>
          </form>
        </div>
      </div>

      <div v-else-if="currentStep === 'success'" class="success-screen">
        <span class="icon-success">✓</span>
        <h4>¡Gracias por contactarnos!</h4>
        <p>Hemos recibido tu mensaje. Te responderemos lo antes posible.</p>
        <p class="ticket-reference">Referencia: #{{ ticketReference }}</p>
        <button class="secondary-button" @click="reset">
          Cerrar
        </button>
      </div>

      <div v-else-if="currentStep === 'error'" class="error-screen">
        <span class="icon-error">⚠</span>
        <h4>Ha ocurrido un error</h4>
        <p>No pudimos enviar tu mensaje. Por favor, inténtalo de nuevo más tarde.</p>
        <p class="error-details" v-if="errorMessage">{{ errorMessage }}</p>
        <button class="primary-button" @click="currentStep = 'form'">
          Volver a intentar
        </button>
      </div>

      <div class="widget-footer">
        <span class="powered-by">Powered by GrowDesk</span>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, computed, onMounted, nextTick, watch, defineEmits } from 'vue';
import { format } from 'date-fns';
import { useWidgetApi } from '../api/widgetApi';

const api = useWidgetApi();

const props = defineProps({
  brandName: {
    type: String,
    default: 'GrowDesk'
  },
  welcomeMessage: {
    type: String,
    default: '¿Necesitas ayuda? Estamos aquí para ayudarte.'
  },
  primaryColor: {
    type: String,
    default: '#1976d2'
  },
  position: {
    type: String,
    default: 'bottom-right'
  },
  logoUrl: {
    type: String,
    default: ''
  }
});

const emit = defineEmits(['open', 'close', 'resize']);

// Estado del widget
const isOpen = ref(false);
const currentStep = ref('welcome');
const isSubmitting = ref(false);
const errorMessage = ref('');
const ticketReference = ref('');

// Formulario
const form = reactive({
  name: '',
  email: '',
  issue: ''
});

const formErrors = reactive({
  name: '',
  email: '',
  issue: ''
});

// Chat
const messages = ref<Array<{text: string, isUser: boolean, timestamp: Date}>>([]);
const newMessage = ref('');
const messagesContainer = ref<HTMLElement | null>(null);

// Computed properties
const widgetTitle = computed(() => props.brandName);

// Abrir/cerrar el chat
const toggleChat = () => {
  isOpen.value = !isOpen.value;
  
  if (isOpen.value) {
    emit('open');
    
    // Si el chat se abre, mostrar mensaje de bienvenida en el chat
    if (currentStep.value === 'chat' && messages.value.length === 0) {
      messages.value.push({
        text: '¡Hola! ¿En qué podemos ayudarte hoy?',
        isUser: false,
        timestamp: new Date()
      });
    }
  } else {
    emit('close');
  }
};

// Validar el formulario
const validateForm = () => {
  let isValid = true;
  
  // Resetear errores
  formErrors.name = '';
  formErrors.email = '';
  formErrors.issue = '';
  
  // Validar nombre
  if (!form.name.trim()) {
    formErrors.name = 'El nombre es obligatorio';
    isValid = false;
  }
  
  // Validar email
  if (!form.email.trim()) {
    formErrors.email = 'El correo electrónico es obligatorio';
    isValid = false;
  } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(form.email)) {
    formErrors.email = 'Formato de correo electrónico inválido';
    isValid = false;
  }
  
  // Validar mensaje
  if (!form.issue.trim()) {
    formErrors.issue = 'Por favor describe tu consulta';
    isValid = false;
  }
  
  return isValid;
};

// Enviar el formulario de contacto
const submitForm = async () => {
  if (!validateForm()) {
    return;
  }
  
  isSubmitting.value = true;
  
  try {
    console.log('Enviando formulario con datos:', { 
      name: form.name, 
      email: form.email, 
      mensaje: form.issue 
    });
    
    // Recopilar metadatos de la página
    const metadata = {
      url: window.location.href,
      referrer: document.referrer || 'directo',
      userAgent: navigator.userAgent,
      screenSize: `${window.screen.width}x${window.screen.height}`
    };
    
    console.log('Metadatos recopilados:', metadata);
    
    // Crear el ticket
    const response = await api.createTicket({
      name: form.name,
      email: form.email,
      message: form.issue,
      metadata
    });
    
    console.log('Respuesta del servidor:', response);
    
    // Guardar la referencia del ticket
    ticketReference.value = response.ticketId;
    
    // Si el chat en vivo está disponible, ofrecer esa opción
    if (response.liveChatAvailable) {
      currentStep.value = 'chat';
      // Iniciar la conversación con un mensaje del sistema
      messages.value.push({
        text: `¡Hola ${form.name}! Un agente revisará tu consulta pronto. Mientras tanto, puedes continuar explicándonos más detalles.`,
        isUser: false,
        timestamp: new Date()
      });
    } else {
      // Mostrar pantalla de éxito si no hay chat disponible
      currentStep.value = 'success';
    }
  } catch (error: any) {
    console.error('Error al enviar el formulario:', error);
    
    // Mostrar mensaje de error
    errorMessage.value = error.message || 'Ocurrió un error desconocido.';
    currentStep.value = 'error';
  } finally {
    isSubmitting.value = false;
  }
};

// Enviar mensaje en el chat
const sendMessage = async () => {
  if (!newMessage.value.trim() || isSubmitting.value) return;
  
  const messageText = newMessage.value;
  newMessage.value = '';
  
  // Añadir mensaje del usuario a la conversación
  messages.value.push({
    text: messageText,
    isUser: true,
    timestamp: new Date()
  });
  
  // Scroll al final de los mensajes
  await nextTick();
  scrollToBottom();
  
  isSubmitting.value = true;
  
  try {
    // Enviar mensaje al backend
    await api.sendMessage({
      ticketId: ticketReference.value,
      message: messageText
    });
    
    // En una implementación real, aquí se conectaría con WebSockets
    // para recibir respuestas en tiempo real
    
    // Simulación de respuesta del agente (solo para demostración)
    setTimeout(() => {
      messages.value.push({
        text: 'Gracias por tu mensaje. Un agente lo revisará pronto.',
        isUser: false,
        timestamp: new Date()
      });
      
      scrollToBottom();
      isSubmitting.value = false;
    }, 1000);
  } catch (error) {
    console.error('Error al enviar mensaje:', error);
    isSubmitting.value = false;
  }
};

// Formatear hora para los mensajes
const formatTime = (date: Date) => {
  return format(date, 'HH:mm');
};

// Scroll al final de los mensajes
const scrollToBottom = () => {
  if (messagesContainer.value) {
    messagesContainer.value.scrollTop = messagesContainer.value.scrollHeight;
  }
};

// Observar cambios en los mensajes para hacer scroll
watch(
  messages,
  () => {
    nextTick(() => {
      scrollToBottom();
    });
  }
);

// Resetear el widget
const reset = () => {
  currentStep.value = 'welcome';
  form.name = '';
  form.email = '';
  form.issue = '';
  messages.value = [];
  newMessage.value = '';
  ticketReference.value = '';
  errorMessage.value = '';
};

// Aplicar estilos personalizados
onMounted(() => {
  // Notificar tamaño inicial del botón
  emit('resize', { width: 60, height: 60 });
});
</script>

